body {
  margin: 0;
  font-family: 'Karla', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
main{
  padding: 20px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
  display:flex;
  height:70px;
  padding: 20px 40px;
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  color: white;
}

.header--title{
  font-size: 24px;
  padding: 5px;
  letter-spacing: -0.05em;
}

.form{
  padding: 20px;
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap:20px;
}
.form--button{
  grid-column: 1/-1; /*takes all the spaces*/
  font-family: 'Karla', sans-serif;
  font-size: 20px;
  border-radius: 5px;
  border: 1px solid #D5D4D8;
  background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
  color: white;
  border: none;
  cursor:pointer;
}
.form--input{
  font-family: 'Karla', sans-serif;
  font-size: 18px;
  border-radius: 5px;
  border: 1px solid #D5D4D8;
  text-indent: 5px;
  
}

.meme {
  position: relative;
}

.meme--image {
  max-width: 100%;
  border-radius: 3px;
}

.meme--text {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}

.bottom {
  bottom: 0;
}

.top {
  top: 0;
}